@charset "UTF-8";

@import 'variables_theme';
@import 'global-header-landing-page';

.container-fluid {
  padding: 0;
}

.dialog-off-canvas-main-canvas > main {
  margin: 0;
}

.page-node-type-landingpage {
  font-size: 1rem;
  line-height: 1.2rem;
  font-weight: 300;

  .section {
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;

    .title-wrapper {
      max-width: 350px;
      margin: 0 auto 15px auto;
    }

    /* Style for our header texts */
    h2 {
      font-size: 34px;
      padding: 0;
      text-transform: uppercase;
      color: #6686A2;
    }

    h3 {
      font-size: 26px;
    }

    .line {
      width: 100%;
      height: 4px;
      margin: 10px auto;
    }

    .read-more {
      margin: 15px 0;
      text-align: right;

      a {
        color: #293749;
      }
    }
  }

  /** About **/
  .about {
    .container {
      margin: 0 auto 0 auto;
      width: 50%;
      @media screen and (max-width: 1024px) {
        width: 60%;
      }
    }

    background: url(../images/pattern-1.png);
    background-color: #7ea2bb;

    h2 {
      color: #eee;
    }

    .line {
      background-color: #eee;
    }

    .field--name-body {
      color: #eee;
      text-align: left;
      margin: 0 auto;
      max-height: 600px;
      font-size: 18px;
      line-height: 24px;

      .align-left {
        @media (max-width: 768px) {
          float: none;
        }
      }

      img {
        border-radius: 50%;
        border: 8px solid #eee;
        margin: 0 30px 15px 0;
        width: 216px;
        height: 216px;
        @media (max-width: 768px) {
          float: none;
          display: block;
          //width: 50%;
          //height: auto;
          margin: 0 auto 15px auto;
        }
      }
    }

    .read-more a {
      color: #eee;
    }
  }

  /** Projects **/
  .projects {
    background: url(../images/bg-page-section--light-dbffac9426e51e0fdcaaad8452d7561b.png);
    background-color: #fff;
    color: #575756;

    h2 {
      color: #6686A2;
    }

    .line {
      background-color: #81a5bf;
    }

    h3 {
      margin-bottom: 15px;

      a {
        color: #293749;
      }
    }

    .field--name-field-landing-page-backgro-media {
      border: 1px solid #81a5bf;
      @media screen and (min-width: 1140px) {
        background-image: url(../images/devices/cs-imac-frame-small.png);
        background-repeat: no-repeat;
        background-position: center top;
        height: 440px;
        width: 555px;
        padding: 19px 31px 0 31px;
        border: none;
      }
    }

    img {
      width: 100%;
      height: auto;
      max-height: 299px;
    }

    .field--name-body {
      font-size: 18px;
      line-height: 24px;
      text-align: justify;
    }

    .read-more a {
      color: #293749;
    }
  }

  /** Projects **/
  .clients {
    padding-top: 0;
    background: url(../images/bg-page-section--light-dbffac9426e51e0fdcaaad8452d7561b.png);
    background-color: #fff;
    color: #575756;

    h2 {
      color: #6686A2;
    }

    .line {
      background-color: #81a5bf;
    }

    .field--name-field-image-media {
      text-align: center;
      @media (min-width: 768px) {
        //text-align: initial;
      }

      img {
        margin: 0 auto;
        width: auto;
        height: auto;
      }
    }
  }

  /** Articles **/
  .articles {
    background: #eaeaea;
    color: #575756;

    h2 {
      color: #6686A2;
    }

    .line {
      background-color: #6686A2;
    }

    h3 a {
      color: #293749;
    }

    .field--name-node-title {
      margin-bottom: 15px;
    }

    .field--name-field-image-media {
      float: left;
      margin: 0 15px 15px 0;

      img {
        border: 1px solid #81a5bf;
        width: 200px;
        height: 133px;
      }
    }

    .field--name-body {
      text-align: left;
      max-height: 600px;
      padding-right: 15px;
    }

    .read-more a {
      color: #293749;
    }
  }

  /** Feedback **/
  .section.feedback {
    background: url(../images/client-testimonials-background.jpg) bottom center;
    background-size: cover;
    background-color: #fff;
    color: #575756;

    h2 {
      color: #6686A2;
    }

    .line {
      background-color: #81a5bf;
    }

    h3.title {
      font-size: 20px;
      color: #293749;
      margin: 0 0 15px 0;
    }

    .about-person {
      margin-bottom: 15px;
      color: #6686A2;

      span {
        color: #6686A2;

        &:not(:last-child) {
          &:after {
            content: "|";
          }
        }
      }
    }

    .name {
    }

    .position {
    }

    .country {
    }

    .field--name-field-image-media {
      img {
        width: 150px;
        height: 150px;
        margin: 0 auto 15px auto;
        border-radius: 50%;
        border: 8px solid #eee;
        opacity: .8;

        &:hover {
          opacity: 1;
        }
      }
    }

    .project-info {
      margin-bottom: 15px;
    }

    .field--name-field-project-title {
      color: #293749;

      a {
      }

      .field__item {
        &:not(:last-child) {
          &:after {
            content: ",";
          }
        }
      }
    }

    blockquote {
      border-left: 1px solid #bbb;
      font-style: italic;
      padding: 0.5em 10px;
      text-align: justify;
      margin: 0;
      font-size: inherit;
      color: #575756;

      &:before, &:after {
        font-family: helvetica, arial, sans-serif;
        color: #6686A2;
        font-size: 3em;
        line-height: 0.1em;
      }

      &:before {
        content: "\201c";
        vertical-align: -.4em;
        margin-left: 0;
        margin-right: 0.2em;
      }

      &:after {
        content: "\201d";
        vertical-align: -.45em;
        margin-left: 0.2em;
      }

      > p:first-child {
        display: inline;
      }
    }

    .read-more a {
      color: #293749;
    }
  }
}

@media screen and (max-width: 1024px) {
  .page-node-type-landingpage {
    .section {
      .title-wrapper {
        max-width: 50%;
      }
    }
  }
}

@media screen and (min-width: 993px) {

  .page-node-type-landingpage {
    .main-container.container {
      padding: 79px 0 0 0;
      width: 100%;
      margin-left: -15px;
      margin-right: -15px;
      margin-bottom: 0;
    }

    .main-section {
      padding: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .page-node-type-landingpage {
    .main-container.container {
      padding-top: 79px;
      padding-bottom: 0;
      width: 100%;
    }

    .main-section {
      padding: 0;
      width: 100%;
    }

    .articles {
      .field--name-field-image-media {
        float: none;
        margin-right: 0;

        img {
          margin: auto;
          width: 100%;
          height: auto;
        }
      }
    }

    .about {
      .container {
        width: 75%;
      }
    }

    .about {
      .field--name-body {
        max-height: initial;
        padding-right: 0;
      }
    }

    .articles {
      .field--name-body {
        max-height: initial;
        padding-right: 0;
      }
    }

    //.contact-form {
    //  width: 75%;
    //}
  }
}

@media screen and (max-width: 768px) {
  .page-node-type-landingpage {
    font-size: 14px !important;
    line-height: 16px !important;

    .field--name-body, .field--name-body {
      font-size: 14px !important;
      line-height: 16px !important;
    }

    .title-wrapper {
      max-width: 80%;
    }

    .section {
      h2 {
        font-size: 28px !important;
      }

      h3 {
        font-size: 22px !important;
      }
    }

    .about .container {
      width: 100%;
    }

    .projects {
      img {
        max-height: initial;
      }
    }

    //.contact-form {
    //  width: 100%;
    //}
  }
}
